import Menu from "./components/Menu";
import AddTask from "./components/AddTask";
import { Outlet, useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import AddProject from "./components/AddProject";
import ProjectsMenu from "./components/ProjectsMenu";
import TaskMaker from "./components/TaskMaker";
import { useSelector } from "react-redux";
import parseISO from 'date-fns/parseISO'
import isThisWeek from 'date-fns/isThisWeek'
import uniqid from 'uniqid'

function App() {
  const [selectedOption, setSelectedOption] = useState('AddTask');
  const [selectedView, setSelectView] = useState('a.links[href="/all-tasks"] p')
  const [filterTasks, setFilterTasks] = useState('all-tasks')

  // useEffect(() => {

  //   document.querySelectorAll('.underline').forEach(el => {
  //     el.classList.remove('underline')
  //   })

  //   document.querySelector(`${selectedView}`).classList.add('underline')

  // }, [selectedView, selectedOption])

  const changeTab = (val, handle) => {
    if (handle === '') {
      setSelectView(`div.project-box#${val}`)
    } else {
      setSelectView(`a.links[href="/${val}"] p`)
    }
    setFilterTasks(val)
  }


  return (

    <div className="App">

      < Menu
        changeTab={changeTab}
      />

      < ProjectsMenu
        changeTab={changeTab}
      />

      {selectedOption === 'AddTask'
        ?
        (

          <AddTask
            setSelectedOption={setSelectedOption}
          />

        )
        :
        (

          <AddProject
            setSelectedOption={setSelectedOption}
          />
        )}

      <div id="main-container">

        <GetTasks
          filter={filterTasks}
        />

      </div>
    </div>
  );
}

function GetTasks({ filter }) {

  const items = useSelector(state => state.items);
  let displayTasks = []

  if (filter === 'all-tasks') {

    displayTasks = items.filter(el => !el[1].tasks && !el[1].done)

  } else if (filter === 'week-tasks') {

    displayTasks = items.filter(el => {
      if (isThisWeek(parseISO(el[1].dueDate), { weekStartOn: 0 })
        && !el[1].done) {
        return (el[1])
      }
    })

  } else if (filter === 'done-tasks') {

    displayTasks = items.filter(el => { return (el[1].done) })

  } else if (filter === 'important-tasks') {

    displayTasks = items.filter(el => { return el[1].important === 'important' && !el[1].done })

  } else {

    let arrOfTasks = []

    const tasksId = items.filter(el => el[0] === filter)[0][1].tasks

    for (let i = 0; i < tasksId.length; i++) {
      const taskId = tasksId[i]

      for (let j = 0; j < items.length; j++) {
        const task = items[j]

        if (task[0] === taskId) {

          arrOfTasks.push(task)
        }

      }
    }

    displayTasks = arrOfTasks
  }


  return (
    <div>
      {displayTasks.map(task => {
        return (
          <TaskMaker
            key={uniqid()}
            task={task}
          />
        )
      })}
    </div>
  )
}
export default App;
